import React from 'react';
import {connect} from 'react-redux';
import './adminlogin.css';
import ReactDOM from 'react-dom';
import {LoginActionForAdmin} from '../../../actions/action.js';
import AOS from 'aos';

const mapStateToProps=(state,ownProps)=>{
	const myState=state.allDetails.payload9;
		
		if(myState.status==="ok"){
			Object.entries(myState).map(([key,value])=>{
				key.startsWith('details')?value.map(g=>{Object.entries(g).map(([a,b])=>{localStorage.setItem(a,b)})}):localStorage.setItem(key,value)})

				setTimeout(()=>{
					document.getElementById("submitadmin").setAttribute("class","btn btn-default form-control disabled")
				},3000);
				setTimeout(()=>{
					window.location='http://htmltemplate.pythonanywhere.com/authorized';
				},4000);
		}else{
			try{
			const b=(<div className="alert alert-danger" data-aos-duration="500" style={{backgroundColor:'red',color:'white'}} data-aos="fade-down">
					<center>{myState.allDetails.payload9.message}</center></div>);
			ReactDOM.render(b,document.getElementById("ErrorResponseTest"));
			setTimeout(()=>{ReactDOM.render("",document.getElementById("ErrorResponseTest"))},3000);
		}catch(e){}
	}

}
class LoginModal extends React.Component{
	constructor(){
		super();
		AOS.init();
		this.state={username:'',password:'',isEnabledButton:'btn btn-default form-control',btnHTML:'Login'}
		this.Sender=this.Sender.bind(this);
	}
	Sender(){
		const myData=(<div className='spinner-border' style={{color:'white'}}></div>);

	}
	render(){
		return(
				<div className="row">
					<br/>
					<div className="col-lg-3"></div>
						<div className="col-lg-6">
							<div className="card shadow p-5" id="admincard">
								<span id='ErrorResponseTest'>{this.props.myErrorTest}</span>
								<div className="card-header">
									<center><h4>Admin Login</h4></center>
								</div>
								<div className="card-body">
									<div className="form-group">
										<i className="fa fa-user"></i><input onChange={(e)=>this.setState({username:e.target.value})} type="text" className="form-control" required placeholder="Username" />
										<i className="fa fa-key"></i><input type="password" onChange={(e)=>this.setState({password:e.target.value})} className="form-control" required placeholder="password" />
										<br/>
										<a type="button" id="submitadmin" onClick={()=>this.props.SendLogin({username:this.state.username,password:this.state.password})} className={this.state.isEnabledButton} >{this.state.btnHTML} </a>
									</div>
								</div>
							</div>
						</div>
					</div>
			);
	}
	
}
const mapDispatchToProps=dispatch=>{
		return{
			SendLogin:(data)=>dispatch(LoginActionForAdmin(data))
		}
	}
export default connect(mapStateToProps,mapDispatchToProps)(LoginModal);