import React,{useState} from 'react';
import './editor.css';
import ReactDOM from 'react-dom';
import DataBase from './database.js';

document.body.addEventListener("click",()=>{
	if(document.querySelectorAll(".list-group > li").length){ReactDOM.render("",document.querySelector(".list-group"))}
})
function TestingPlease(props1,props2){
	if(props1.Status==="nav-link form-control"){
		props2({Status:'nav-link form-control disabled',EditPencilIcon:'fa fa-check'})
	}else{props2({Status:'nav-link form-control',EditPencilIcon:'fa fa-pencil'})}
	const ts=document.querySelectorAll("#databaselist >li >ul");
	ts.forEach(keyhandler=>{
		[...keyhandler.children].map(u=>{
			u.childNodes[0].setAttribute("class",props1.Status)
			})
		})	
	}
function CheckingEnabled(firsthandler,secondhandler){
	if(firsthandler.checkShowIcon==="fa fa-eye-slash rounded-circle"){
		secondhandler({checkShowIcon:"fa fa-eye rounded-circle",isDatadisplay:"none"});
		<DataBase checkEnabled={{display:firsthandler.isDatadisplay}} />
		}else{secondhandler({checkShowIcon:"fa fa-eye-slash rounded-circle",isDatadisplay:"inline"});
			<DataBase checkEnabled={{display:firsthandler.isDatadisplay}} />
	}
}
function Popupdelete(){
	const p=(<div className="modal fade" id="deletemodal" aria-hidden="true" role="dialog">
				<div className="modal-dialog">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title">Are You Sure</h5>
								<a type="button" className="close" data-dismiss="modal"><i className="fa fa-close"></i></a>

							</div>
							<div className="modal-body">
								<center><a type="button" id="firstmaker" className="btn btn-default">Delete Table</a>
								<br/><br/>
								<a type="button" id="secondmaker" className="btn btn-default">Cancel </a>
								</center>
							</div>
						</div>
				</div>
			</div>);
			ReactDOM.render(p,document.getElementById("usermodal"));
}
function InputChanger(e){
	const mySelector=document.querySelectorAll("#databaselist > li > ul >li:nth-child(1)");
	ReactDOM.render(<div id="spintesting"><div className="spinner-border"></div><br/><br/><span>Searching...</span></div>,document.querySelector(".list-group"));

	setTimeout(()=>{
	const userArray=[...mySelector].map(inputuser=>{
		const myValue=inputuser.childNodes[0].value;
		if(myValue.includes(e.target.value)||myValue.includes(e.target.value.toUpperCase()||myValue.includes(e.target.value.toString().toUpperCase()))){
			return <li className="list-group-item"><a className="btn btn-default">{myValue}</a></li>}
		})
	ReactDOM.render(userArray,document.querySelector(".list-group"));
	},500)
}

function CreateTable(){
	const p=(<div className="modal fade" id="dbmodal" aria-hidden="true" role="dialog">
				<div className="modal-dialog">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title">Change Table</h5>
								<a type="button" className="close" data-dismiss="modal"><i className="fa fa-close"></i></a>

							</div>
							<div className="modal-body">
								<center>
									<input type="text" id="inputcheck" placeholder="Table Name..." className="form-control" />
									<br />
									<a type="button" id="firstmaker" className="btn btn-default">Connect</a>
								</center>
							</div>
						</div>
				</div>
			</div>);
			ReactDOM.render(p,document.getElementById("usermodal"));
}
const EditorSql=(props)=>{
	const [first,second]=useState({checkShowIcon:"fa fa-eye-slash rounded-circle",isDatadisplay:'inline',EditPencilIcon:'fa fa-pencil',Status:'nav-link form-control'});
	return(
			<React.Fragment>
				<div id="navtest" >
					<div className="form-inline" id="dbform">
						<i className="fa fa-search"></i>
						<input type="text" onKeyUp={InputChanger} placeholder="Search Query..." className="form-control" />
						<br/>

					</div>
				
					<div id="usermodal"></div>
					<ul className="nav nav-pills">
						
						<li className="nav-item"><a className="nav-link" onClick={()=>TestingPlease(first,second)}><i className={first.EditPencilIcon}></i></a></li>
						<li className="nav-item"><a className="nav-link" data-toggle="modal"  data-target="#deletemodal" onClick={Popupdelete}><i className="fa fa-trash"></i></a></li>
						<li className="nav-item"><a onClick={()=>CheckingEnabled(first,second)} className="nav-link"><i  
							className={first.checkShowIcon}
							
							>
							</i></a></li>
						<li className="nav-item"><a onClick={CreateTable} data-toggle="modal" data-target="#dbmodal" className="nav-link"><i className="fa fa-plus"></i></a></li>
					</ul>
					<br/>
				</div>
				<div id="searchtest">
						<ul className="list-group"></ul>
					</div>
			</React.Fragment>
		);

}
export default EditorSql;