import React,{useEffect} from 'react';
import './adminsidebar.css';
import ReactDOM from 'react-dom';
const SidebarAdmin=()=>{
	useEffect(()=>{
		setInterval(()=>{
			const checkdate=new Date();
			const myDate=checkdate.toString().split(" ")
			const timerender=(<React.Fragment>{myDate[4]+" "+ myDate[5]}
				</React.Fragment>);
			ReactDOM.render(timerender,document.getElementById("user_time"));
		},1000);
	},[]);
	const img_admin="https://img.pngio.com/administrator-icon-administrator-png-256_256.png";
	return(

			<div className='sidebar' id="adminjumbo">
				<center><h4 id="admintitle"><img src={img_admin} className="img-fluid"/><br/>AdminInterface</h4>
				<div id="user_time" style={{color:'white'}}></div></center>
				<hr style={{width:'100%',backgroundColor:'white'}}></hr>
				<ul className="nav nav-tabs flex-column" id="testjumbo">
					<li className="nav-item active"> 
					<a type="button" className="nav-link" href="#home" role="tab" aria-control="hometabtest" data-toggle="tab"><i className="fa fa-home"></i>&nbsp;&nbsp;Home</a></li>
					<li className="nav-item"><a type="button" role="tab" aria-controls="edittabtest"  aria-selected="false" data-toggle="tab" href="#edit" className="nav-link"><i className="fa fa-pencil"></i>&nbsp;&nbsp;Edit Data</a></li>
					<li className="nav-item"><a type="button" role="tab" aria-controls="settingtabtest" aria-selected="false" data-toggle="tab" className="nav-link" href="#settings"><i className="fa fa-cog"></i>&nbsp;&nbsp;Settings</a></li>
					<li className="nav-item"><a type="button" aria-controls="logouttabtest" className="nav-link"><i className="fa fa-sign-out">&nbsp;&nbsp;</i>Signout</a></li>
				</ul>
			</div>
		);

}
export default SidebarAdmin;