import {useRef} from 'react';

export const Loginhandler=(data0)=>{
	return (dispatch)=>{
		const loginsend=({
			method:'POST',
			headers:{'Content-Type':'application/json','Origin':'http://htmltemplate.pythonanywhere.com'},
			body:JSON.stringify({username:data0.loginemail,password:data0.loginpassword})
		});
		async function Senddata(){
			const a=await fetch("http://htmltemplate.pythonanywhere.com/user/api/login",loginsend);
			return await a.json();
		}
		Senddata().then((response)=>{
			if(response.ok){
				dispatch({
					type:'LOGIN_USER',
					payload:response
				});
			}
			dispatch({
				type:'LOGIN_USER',
				payload:response
			});
		});
	}
}
