export const DeleteAccount=(data8)=>{
	return(dispatch)=>{
		const a=({
			method:'DELETE',
			headers:{'Content-Type':'application/json','Origin':'http://htmltemplate.pythonanywhere.com'},
			body:JSON.stringify(data8)
		});
		async function DeleteUser(){
			const c=await fetch("http://htmltemplate.pythonanywhere.com/user/api/delete",a);
			return await c.json();
		}
		DeleteUser().then((response)=>{
			if(response.ok){
				setTimeout(()=>{
					window.location='http://htmltemplate.pythonanywhere.com/register';
				},3000)
			}dispatch({
				type:'DELETE_ACCOUNT_ERROR',
				payload:response
			});				
		});
	}
}
