import React,{useEffect} from 'react';
import io from 'socket.io-client';
import $ from 'jquery';
import ReactDOM from 'react-dom';
import  './createadminuser.css';

	const sio=io("http://htmltemplate.pythonanywhere.com/token");

function TableDisplay(){
	return(
			<div className="form-group" id="tableform">
				<ul className="nav" id="firstul" >
					<li className="nav-item"><input type="text" required placeholder="Username ex(example@mail.com)"
						 className="form-control" /></li>
					<li className="nav-item"><input required type="password" placeholder="Password"
											 className="form-control" /></li>
					<li className="nav-item"><input required type="text" placeholder="Firstname"
											 className="form-control" /></li>
					<li className="nav-item"><input required type="text" placeholder="Lastname"
											 className="form-control" /></li>
					<li className="nav-item"><input required type="text" onFocus={UserToken} id="tokenholder" placeholder="Token"
											 className="form-control" /><div id="spinuser"></div></li>
					<li className="nav-item"><input required type="text" placeholder="Role"
											 className="form-control" /></li>
				</ul>
				<br />
				<ul className="nav" id="checkboxul">
					<li className="nav-item"><input type="checkbox" id="all" name="all" value="allgranted" /></li>
					<label for="all">Grant all privileges </label>

					<li className="nav-item"><input type="checkbox" id="update" value="update" /></li>
					<label for="update">Update </label>

					<li className="nav-item"><input type="checkbox" id="drop" value="drop" /></li>
					<label for="drop">drop </label>

					<li className="nav-item"><input type="checkbox" id="truncate" value="truncate" /></li>
					<label for="truncate">Truncate </label>

					<li className="nav-item"><input type="checkbox" id="alter" value="alter" /></li>
					<label for="alter">Alter </label>

					<li className="nav-item"><input type="checkbox" id="Delete" value="Delete" /></li>
					<label for="all">Delete</label>

					<li className="nav-item"><input type="checkbox" id="flush" value="flush" /></li>
					<label for="flush">Flush </label> 

					<li className="nav-item"><input type="checkbox" id="Insert" value="Insert" /></li>
					<label for="Insert">Insert </label>

					<li className="nav-item"><input type="checkbox" id="Change" value="Change" /></li>
					<label for="Change">Change </label>

					<li className="nav-item"><input type="checkbox" id="Modify" value="Modify" /></li>
					<label for="Modify">Modify </label>

					<li className="nav-item"><input type="checkbox" id="Rename" value="Rename" /></li>
					<label for="Rename">Rename </label>
				</ul>
				<a type="button" id="createbutton" className="btn btn-default">Create New </a>
			</div>	
		);
}

function UserToken(e){
	const mySpiner=(<div id="checkspin" className="spinner-border"></div>);
			ReactDOM.render(mySpiner,document.getElementById("spinuser"));


			sio.on("ReceviedConnect",(e)=>{
				alert()
			});
			sio.on("disconnect",()=>{
				console.log("disconnected");
			})
			sio.on("Disconnected_Request",(e)=>{
				console.log("Server Cannot Established Connection To the Server....");
			});
			sio.on("ReceiveToken",(e)=>{
				ReactDOM.render(e.data,document.getElementById("tokenholder"));
			});
			e.target.setAttribute("disabled","");
			setTimeout(()=>{ReactDOM.render("",document.getElementById("spinuser"))},2000);
}


function CreateOperation(e){
	e.target.value==="CreateAdmin"?ReactDOM.render(<TableDisplay />,document.getElementById('formtest')):ReactDOM.render("",document.getElementById("formtest"));
}
const AdminUserCreate=()=>{				

	useEffect(()=>{
			sio.on("connect",(e)=>{
				alert();
			});
	},[sio])

	return(
			<React.Fragment>
				<select id="myoptions" className="form-control" onChange={CreateOperation}>
					<option selected="selected">--Select following--</option>
					<option value="CreateAdmin">Create User</option>
				</select>
				<br />
				<div id="formtest"></div>
			</React.Fragment>
		);
}
export default AdminUserCreate;