import React from 'react';
import Header from '../../components/header/header.js';
import covidimg from './covid.jpg';
import Footer from '../../components/footer/footer.js';
import AOS from 'aos';
import ReactDOM from 'react-dom';
import {Sender} from '../../components/header/header.js';
import {FeedbackSubmit} from '../../actions/action.js';
import './feedback.css';
import {connect} from 'react-redux';

const mapStateToProps=state=>{
		return{
			myStateResponse:state.allDetails.payload14.message
		}
	}

class Feedback extends React.Component
{
	constructor()
	{
		super();
		AOS.init();
		this.state={Email:'',Query:'',test:{spinCheck:'Submit',btnStatus:'btn btn-default'}};
		document.body.style.overflowY="scroll";
		this.SendUser=this.SendUser.bind(this);
	}
	SendUser(){
		this.props.SendFeedback({Email:this.state.Email,query:this.state.Query,authorization:localStorage.getItem("authorization")});
		const myspinner=(<div className="spinner-border"></div>);
		this.setState({test:{spinCheck:myspinner,btnStatus:'btn btn-default disabled'}});
	}
	componentDidUpdate(){
		setTimeout(()=>{
				if(this.props.myStateResponse){
					if(this.state.test.spinCheck !="Submit"&&this.state.test.btnStatus==="btn btn-default disabled"){
						setTimeout(()=>{

					this.setState({test:{spinCheck:'Submit',btnStatus:'btn btn-default'}});
					ReactDOM.render(<div className="alert alert-success" id="responsealert" data-aos="fade-down" data-aos-duration='400'>
						<center>{this.props.myStateResponse}</center></div>,document.getElementById('response'))},500)
					}
					}},2000);

				setTimeout(()=>{
					ReactDOM.render("",document.getElementById("response"));
				},4000)
	}

	render(){
				
		const indexstyle={boxShadow:'0px 5px',borderLeft:'none',transition:'0.3s boxShadow',color:'#1DD9D8'};

		return(
				<>
					<Header feedbacklink={'nav-item'} feedbackstyle={indexstyle}
						homeuser={<a className="nav-link" href="/">Home</a>}
						loginuser={<a className="nav-link" href="/login">Login</a>}
						feedbackuser={<a className="nav-link" href="/feedback">Feedback</a>}
						aboutuser={<a className="nav-link" href="/about">About</a>}
						registeruser={<a className="nav-link" href="/register">Signup</a>}
					/>
						<br/><br /><br />
						<div className="container">
							<div className="row">
								<div className="col-lg-6">
									<div className="jumbotron" id="feedjumbo" data-aos="fade-right" data-aos-duration="1000">
										<h4> We Should Sanitize </h4>
										<br/>
										<h4> Don't Forogt to Wear Mask</h4>
										<br/>
										<h4> Stay Home Stay Safe</h4>
										<br/>
										<span> We are together to Kill a coronavirus and also we will win the fight
											and covid will lose his fight from india Wear a mask and protect youself from sepreating or target
										</span>
									</div>
								</div>
								<div className="col-lg-6">
									<img className="img-fluid" data-aos="fade-left" data-aos-duration="1000" id="covidimg" src={covidimg} />
								</div>

							</div>
							<div className="row">
								<div className="col-lg-3"></div>
								<div className="col-lg-6">
									<div className="jumbotron" id="feedbackjumbo">
										<span id="response"></span>
										<center><h4>Feedback form</h4></center>
										<br />
										<div className="form-group">
											<input type="text" required placeholder="Enter Your Email/Username" onChange={(e)=>this.setState({Email:e.target.value})} className="form-control"/>
											<br/>
											<textarea rows="5"  required className="form-control" onChange={(e)=>this.setState({Query:e.target.value})} placeholder="Insert Your Feedback" cols="5"></textarea>
											<br/>
											<a type="submit" id="sendbtn" onClick={this.SendUser} className={this.state.test.btnStatus}>{this.state.test.spinCheck}</a>
										</div>
									</div>
								</div>
							</div>
							<div className="col-lg-3"></div>
						</div>
					<Footer />
				</>

			);

	}
}
const mapDispatchToProps=dispatch=>{
	return{
		SendFeedback:(data)=>{
			dispatch(FeedbackSubmit(data))
		}
	}
}
export default connect(mapStateToProps,mapDispatchToProps)(Feedback);