import React from 'react';
import L from 'leaflet';
import {useEffect,useRef} from 'react';
import $ from 'jquery';
import './ambulancemap.css';
import CircularProgress from '@material-ui/core/CircularProgress';
import ReactDOM from 'react-dom';
import './ambulancemap.css';

const some_styles={color:'white',height:'100px',width:'100px',left:'650px',top:'200px',position:'absolute',fontWeight:'lighter'};
$("#root").css("visibility","hidden");
setTimeout(()=>{
	ReactDOM.render("",document.getElementById("testing"));
	document.getElementById("root").removeAttribute("style");
},1500);


const MapHandler=()=>{
		const TemporaryArray=[];

	const MyObject=[];
	const testingref=useRef();
		const MyTest={};
		let myMap=null;
		const tempArray=new Set();
		const selectdata=JSON.parse(localStorage.getItem("Hospitals")).Hospitals;
		
	function SuperTest(e){
		ReactDOM.render(<CircularProgress id="superid" size="2rem"/>,document.getElementById("myspin"));
		setTimeout(()=>{
				const bsbs=selectdata.map(cordinater=>{
					if(cordinater.City===e.target.innerHTML){
						const lang=cordinater.Location.split(",")[0].replace("N","").replace("","").replace(" ","");
						const long=cordinater.Location.split(",")[1].replace("E","").replace("","").replace(" ","");
						return [lang,long]
					}
				})
				const myFilter=bsbs.filter(e=>e!==undefined)
				TemporaryArray.map(uu=>{myMap.removeLayer(uu)});
				TemporaryArray.length=0;
				myFilter.map(j=>{
					TemporaryArray.push(L.marker(j,{radius:10,color:'blue',weight:0}).addTo(myMap));
				})
					ReactDOM.render("",document.getElementById("myspin"));
			},1500);

	}
	function Placer(e){
		const p=[...tempArray].map(keyhandler=>{
			if(keyhandler.includes(e.target.value) || keyhandler.includes(e.target.value.toUpperCase())|| keyhandler.toLowerCase().includes(e.target.value)){
				return <li className="nav-item" onClick={SuperTest}> <i className="fa fa-ambulance"></i><a type="button"  style={{width:'100%'}}className="nav-link">{keyhandler}</a><div id="myspin"></div></li>

			}

		});
		ReactDOM.render(p,document.getElementById("stateultest"));
	}

	useEffect(()=>{
		alert()
		const backimg=React.createElement("div",{id:'backimage'},
			React.createElement("img",{src:'https://images.unsplash.com/photo-1530305408560-82d13781b33a?ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8cGxhaW58ZW58MHx8MHx8&ixlib=rb-1.2.1&w=1000&q=80',
				id:'backfullimage'}),
		React.createElement("div",{className:'spinner-border',id:'spinid',style:some_styles}),
		React.createElement("span",{id:'spanid'},"Insitializing the Map...")
		);

		ReactDOM.render(backimg,document.getElementById("testing"));

		selectdata.map(cordinates=>{
			const finalarray=cordinates.Location.split(",");
			const renderObj={[cordinates.City]:[finalarray[0],finalarray[1]]};
			MyObject.push(renderObj);
		})

	},[]);

	
	useEffect(()=>{
	
		selectdata.map(key=>{
			tempArray.add(key.City);
		});
		const superArray=[...tempArray].map(key=>{
			return <li className="nav-item" onClick={TrackerMap}> <i className="fa fa-ambulance"></i><a type="button"  style={{width:'100%'}}className="nav-link">{key}</a><div id="myspin"></div></li>
				
			});
		ReactDOM.render(superArray,document.getElementById('stateultest'));		

 myMap=L.map(document.getElementById("usermap")).setView([20.5937,78.9629],5);

	function TrackerMap(e){
		testingref.current=e.target.innerHTML;
		ReactDOM.render(<CircularProgress  id="superid" size="2rem"/>,document.getElementById("myspin"));
		setTimeout(()=>{
				const bbs=selectdata.map(cordinatehandler=>{
					if(cordinatehandler.City===e.target.innerHTML){
						const lang=cordinatehandler.Location.split(",")[0].replace("N","").replace("","").replace(" ","");
						const long=cordinatehandler.Location.split(",")[1].replace("E","").replace("","").replace(" ","");
						return [lang,long]
					}
				})
				const myFilter=bbs.filter(e=>e!==undefined)
				TemporaryArray.map(uu=>{myMap.removeLayer(uu)});
				TemporaryArray.length=0;
				myFilter.map(j=>{
					TemporaryArray.push(L.marker(j,{radius:10,color:'blue',weight:0}).addTo(myMap));
					myMap.flyTo(j,15,{
						animate:true,
						duration:2
					});
				})
					ReactDOM.render("",document.getElementById("myspin"));
			},1500);
	}
    L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}', {
		    	maxZoom: 15,
		    	fullscreenControl:true,
		   	 	id: 'mapbox/streets-v11',
		    	attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
		    	tileSize: 512,
		    	zoomOffset: -1,
			    accessToken: 'pk.eyJ1Ijoic3VwZXJndXNocGEiLCJhIjoiY2twZmJsZWZxMGdkcTJvcmkzdHAxbGhpYiJ9.uDRrLbheGeP2BnKcO69coQ'
			}).addTo(myMap);

});
	return(
			<>
				<div className="row">
					<div className="col-lg-3">
						<div className="sidebar" id="mapside">
							<div className="form-inline" id="mymapform">
								<i className="fa fa-search" id="msearch"></i><input type="text" id="myInputs" ref={testingref} onKeyUp={Placer} placeholder="Search Location For Ambulance" className="form-control"/>
								<div id="spintesting"></div>
							</div>
							<br/>
							<br/><br/>
							<ul className="nav navbar-nav" id='stateultest'>
							
							</ul>

						</div>
						<div id="barbtn">
								<a type="button" id="sdbtn" className="btn btn-default"><i id="mybars" className="fa fa-bars"></i></a>
						</div>
					</div>
					<div className="col-lg-9">
						<div id="usermap" style={{height:'100%',display:'block'}}></div>
					</div>
				</div>
			</>
		);
}
export default MapHandler;