import './App.css';
import {Route,BrowserRouter,Redirect} from 'react-router-dom';
import Indexing from './container/index/index.js';
import Login from './container/login/login.js';
import About from './container/about/about.js';
import Feedback from './container/feedback/feedback.js';
import Register from './container/register/register.js';
import Admin from './container/admin/admin.js';
import Verify from './container/verify/verify.js';
import Ambulance from  './container/ambulance/ambulance.js';
import Accounts from './container/accounts/accounts.js';
import AdminInterfaceLogin from './container/adminlogin/adminlogin.js';
import AdminInterface from './container/authorized/authorized.js';
import MapHandler from './components/trackermap/ambulancemap.js';

function App() {
 return(
  <BrowserRouter>
    <Route exact path="/" component={Indexing}/>
    <Route exact path="/login" component={Login}/>
    <Route exact path="/feedback" component={Feedback}/>
    <Route exact path="/register" component={Register}/>
    <Route exact path="/verify" component={Verify}/>
    <Route exact path="/about" component={About}/>
    <Route exact path="/admin" component={Admin}/>
    <Route exact path="/authorized" component={AdminInterface}/>
    <Route exact path="/admin/login" component={AdminInterfaceLogin}/>
    <Route exact path="/ambulance" component={Ambulance}/>
    <Route exact path="/accounts" component={Accounts}/>
    <Route exact path="/map" component={MapHandler}/>
  </BrowserRouter>
  );
}

export default App;
