import React,{useState,useEffect} from 'react';
import {useDispatch,useSelector} from 'react-redux';
import ReactDOM from 'react-dom'
import {AdminAccount} from '../../actions/action.js';
import './settings.css';

const GetAdmins=()=>{
	
	const bw=useSelector(state=>{
		const myState=state.allDetails.payload11.data;
		try{
		if(myState.length){
			const wd=myState.map(k=>{

			const bbe=Object.entries(k).map(([key,values])=>{
						return <li className='nav-item'><input type="text" className="form-control nav-link disabled" value={values}/></li>
					});
					ReactDOM.render(bbe,document.getElementById("adminultest"))
			})
		}}catch(e){}
	})

	const [myState,myState1]=useState(localStorage);
	const dispatch=useDispatch();

		useEffect(()=>{
			dispatch(AdminAccount(myState));
		},[]);

	return(
			<React.Fragment>
				<center><h5>Admin Users</h5></center>
				<hr style={{width:'100%'}}></hr>
				<ul className="nav nav-pills" id="flexul">
					<li className="nav-item"> <span className="nav-link">Password</span></li>
					<li className="nav-item"> <span className="nav-link">Type</span></li>
					<li className="nav-item"> <span className="nav-link">Access_token</span></li>
					<li className="nav-item"> <span className="nav-link">Emailid</span></li>
				</ul>
				<ul className="nav" id="adminultest"></ul>

			</React.Fragment>
		);
}
export default GetAdmins;