import React,{useState,useEffect,useRef} from 'react';
import ReactDOM from 'react-dom';
import {useSelector,useDispatch} from 'react-redux';
import './hospital.css';
import $ from 'jquery';
import AOS from 'aos';
import CircularProgress from '@material-ui/core/CircularProgress'
import {GetHospitals,ApplyHospital} from '../../actions/action.js';


const Hospital=(props)=>{
		let bk=useSelector(state=>state.allDetails.payload15);
		AOS.init();
		const [a,b]=useState({btnStatus:'nav-link btn btn-xs',btnState:'fa fa-check',authorization:localStorage.getItem('authorization'),emailid:localStorage.getItem('email')});

		const dispatcher=useDispatch();
		const myStatehandler=useSelector(state=>{return state.allDetails.payload15});

	function ApplyForNew(e){
		const anchor=e.target.parentElement;
		anchor.setAttribute("class","nav-link btn btn-xs disabled");
		ReactDOM.render(<CircularProgress />,anchor);
		const bs={authorization:localStorage.getItem('authorization'),emailid:localStorage.getItem('email'),firstname:localStorage.getItem("Firstname"),lastname:localStorage.getItem("Lastname"),phone:localStorage.getItem("Phone")};

		dispatcher(ApplyHospital(bs));
	try{

				setTimeout(()=>{
					const bs=(<div className="alert alert-danger"  
							style={{position:'fixed',zIndex:100,width:'100%',backgroundColor:'red',color:'white'}}
							data-aos="fade-down" data-aos-duration="800"><center>{bk.message}</center></div>);
					ReactDOM.render(bs,document.getElementById("errorhandleruser"));
					b({btnState:'fa fa-check'});
					anchor.setAttribute("class","nav-link btn btn-xs");
					ReactDOM.render(<i  onClick={ApplyForNew} className={a.btnState}></i>,
						anchor);
			},3000);
			setTimeout(()=>{
				ReactDOM.render("",document.getElementById("errorhandleruser"));
			},4500);

		}catch(e){}
	}

	function Appender(state,mapul,starting,partition){
		const newsize=state.allDetails.payload3.Hospitals.slice(starting,partition);
		const bw=newsize.map(counter=>{
				return(
						<>
							<hr style={{width:'100%'}}></hr>

							<ul className="nav nav-pills" style={{alignItems:'center'}}>
								<li className="nav-item"><span className="nav-link">{counter.Sr}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Name}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.City}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.State}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Telephone}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.TotalVentilators}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Totalbeds}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Vacantbads}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Reservedbads}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Ventilatorsreserved}</span></li>
								<li className="nav-item"><span className="nav-link">{counter.Ventilatorvacant}</span></li>
								<li className="nav-item"><a className="nav-link btn btn-xs"
									><i  onClick={ApplyForNew} className={a.btnState}
									style={{fontSize:'25px'}}

									></i></a></li>
								

							</ul>
						</>
						);
			});
			const newelement=document.createElement("div");
			newelement.setAttribute("id",mapul);
			document.getElementById("dataresponseul").appendChild(newelement);
			ReactDOM.render(bw,document.getElementById(mapul));
	}

	document.body.style.overflowY="scroll";

	const selector=useSelector(state=>{
	try{
		if(state.allDetails.payload3.length != -1){
				let mapul=0;let partition=10;let starting=0;
				const myResponse=document.getElementById("updateresponse");
				Appender(state,mapul,starting,partition);
				$(window).on('scroll',()=>{
					if($(window).scrollTop() + $(window).height() == $(document).height()){
						const b=(<div className="spinner-border" style={{height:'60px',width:'60px'}}></div>);
						ReactDOM.render(b,document.getElementById("loading"));
						setTimeout(()=>{
							ReactDOM.render("",document.getElementById("loading"))

							mapul+=1;partition+=10;starting+=10
							Appender(state,mapul,starting,partition);

						},2000);
					}
				});
		}}catch{}	
	});

	useEffect(()=>{
		$("#root").removeAttr("visibility");

		dispatcher(GetHospitals({authorization:a.authorization,email:a.emailid}))
	},[selector])
	return(
		<>
			<div className="row" id="myrow">
				<div className="col-lg-12">
					<div id="dataul">
						<div id="errorhandleruser"></div>

						<ul className="nav nav-pills" id="myhospitalul">
							<li className="nav-item"><span className="nav-link">S.no</span></li>	
							<li className="nav-item"><span className="nav-link">Hostpital</span></li>	
							<li className="nav-item"><span className="nav-link">City</span></li>	
							<li className="nav-item"><span className="nav-link">State</span></li>	
							<li className="nav-item"><span className="nav-link">Telephone</span></li>	
							<li className="nav-item"><span className="nav-link">Total.V</span></li>	
							<li className="nav-item"><span className="nav-link">Total.B</span></li>	
							<li className="nav-item"><span className="nav-link">Vacant.B</span></li>	
							<li className="nav-item"><span className="nav-link">Reserved.B</span></li>	
							<li className="nav-item"><span className="nav-link">Ventilators.R</span></li>	
							<li className="nav-item"><span className="nav-link">Ventilator.V</span></li>
							<li className="nav-item"><span className="nav-link">Apply</span></li>								
						</ul>

					</div>
						<center><div id="updateresponse"></div></center>

					<div id="dataresponseul"></div>
				</div>
				<span id="loading"></span>
			</div>
			</>
		);

}
export default Hospital;