import React from 'react';
import ReactDOM from 'react-dom';

const SearchForm=(data)=>{

	const AvailableHelps=[
		{
			'helpname':'Covid Vaccination',
			'url':'#'
		},
		{
			'helpname':'How to Save at home',
			'url':'#'
		},
		{
			'helpname':'Precaution Tips',
			'url':'#'

		},
		{
			'helpname':'Delta Varient Covid19',
			'url':'#'
		},
		{
			'helpname':'Doctor Tips',
			'url':'#'
		},
		{
			'helpname':'Help and Support',
			'url':'#'
		},
		{
			'helpname':'Who Advice for Covid19',
			'url':'#'
		}

	]
	if(data!=""){
		document.getElementById("mysearchformjumbo").style.display="inline";		
		setTimeout(()=>{
			const DataReturn=AvailableHelps.map(FindInfo=>{
					if(FindInfo.helpname.includes(data)||FindInfo.helpname.includes(data.toUpperCase())||FindInfo.helpname.replace(" ","").includes(data)||FindInfo.helpname.toLowerCase().includes(data)){
						return (<React.Fragment><li className="nav-item" style={{display:'flex',alignItems:'center',paddingLeft:'5px'}}><i className="fa fa-search"></i><span className="nav-link">{FindInfo.helpname}</span></li></React.Fragment>)
						
					}		
			});
					ReactDOM.render(DataReturn,document.getElementById('mysearchformjumbo'));

		},1000);
	}ReactDOM.render("",document.getElementById("mysearchformjumbo"));
}
export default SearchForm;