import  React from 'react';
import {VictoryChart,VictoryArea,VictoryTooltip,VictoryTheme,VictoryLine,VictoryVoronoiContainer} from 'victory';
import {useSelector} from 'react-redux';
import {useEffect,useState} from 'react';
import {GetGraphStatus} from '../../actions/action.js';

const GraphCurrentHandler=()=>{
		const [an,bn]=useState({datahandler:0,maximum:[],finalMax:0});
		const getState=useSelector(state=>state.allDetails.payload12.data);

			function Gethandler(){
				try{
					if(getState.length){
						const b=getState.map(k=>{
							bn(previous=>({maximum:[...previous.maximum,parseInt(k.TotalCase.toString().replace(/,/g,""))]}));
							return {'x':Math.floor(Math.random()*(12-1))+1,'y':k.TotalCase}
						});
						return b
					}
				}catch(e){}
			}
		useEffect(()=>{
			Gethandler();
		},[])

	return(
			<>
				<VictoryChart
					containerComponent={
						<VictoryVoronoiContainer
							mouseFollowTooltips
							voronoiDimension="x"
							labels={({datum})=>`TotalCases+${datum.y}`}
							/>
					}

				  style={{background:{fill:'#F9BE9E'}}}
				  theme={VictoryTheme.material}
				  width={1000}
				  height={600}
				  domain={{y:[0,Math.max(...an.maximum)],x:[0,Math.max(...an.maximum)]}}

				>
				  <VictoryLine
				    style={{
				      data: { stroke: "red",strokeWidth:5},
				      parent: { border: "1px solid #ccc"}
				    }}
				    data={an.datahandler}
				    interpolation="natural"
				    theme={VictoryTheme.material}
				  />
				</VictoryChart>  

			</>
		);
}
export {GraphCurrentHandler};

