import React from 'react';
import ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import AOS from 'aos';
import './register.css';
import {Registerhandler} from '../../actions/action.js';

const mapStateToProps=state=>{
	const ENDPOINT_REDIRECT="http://htmltemplate.pythonanywhere.com/verify";
	try{
		if(state.allDetails.payload2.status==="success"){

			setTimeout(()=>{
				document.getElementById('regbtn').setAttribute("class","btn btn-default disabled");
			},2000);

			setTimeout(()=>{
				localStorage.setItem('authorization',state.allDetails.payload2.uid)
				localStorage.setItem('email',state.allDetails.payload2.email)
				window.location=ENDPOINT_REDIRECT;
			},4000);
		}else{
			if(state.allDetails.payload2.length!=0){
				setTimeout(()=>{
					const n=(<div className="alert alert-danger" style={{backgroundColor:'red',color:'white'}} data-aos='fade-down' 
						data-aos-duration='1000'><center>{state.allDetails.payload2.message}
						</center></div>);
				ReactDOM.render(n,document.getElementById("Errusers"));			
			},4000)
		}

	}			setTimeout(()=>{ReactDOM.render("",document.getElementById("Errusers"))},6000);
}catch(e){alert(e)}
}
class RegisterSystem extends React.Component
{
	constructor(props){
		super(props);
		AOS.init();
		this.state={Firstname:'',Lastname:'',Phone:'',Address:'',Username:'',Password:''}
		this.Changestate=this.Changestate.bind(this);
	}
	Changestate(e){
			this.setState({[e.target.name]:e.target.value});
	}
	render()
	{

		return(
				<div className="container">
					<div className="row">
						<div className="col-lg-3"></div>
						<div className="col-lg-6">
													<div id="Errusers"></div>

							<div className="card shadow p-4" id="mylogincard">
								<div className="card-header">
									<center><h4 style={{color:'#E3A4BB'}}>Signup</h4></center>
								</div>
								<div className="card-body">
									<div className="form-group">
										<br/>
										<input type="text" onChange={this.Changestate} name="Firstname" placeholder="Firstname" className="form-control"/>
										<br/>
										<input type="text" onChange={this.Changestate} name="Lastname" placeholder="Lastname" className="form-control"/>
										<br/>
										<input type="tel" onChange={this.Changestate} name="Phone" placeholder="Phone" className="form-control"/>
										<br/>
										<input type="text"  onChange={this.Changestate} name="Address" placeholder="Address" className="form-control"/>
										<br/>
										<input type="email" onChange={this.Changestate} name="Username" placeholder="example@mail.com" className="form-control"/>
										<br/>
										<input type="password" onChange={this.Changestate} name="Password" placeholder="Password" className="form-control"/>
										<br/>
										<a type="button" id="regbtn" onClick={()=>this.props.RegistrationUser(this.state)} className="btn btn-default"><center style={{color:'white'}}>Create Account</center></a>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

			);
	}
}

const mapDispatchToProps=(dispatch)=>{
	return{
		RegistrationUser:data=>{
			dispatch(Registerhandler(data))
		}
	}
}
export default connect(mapStateToProps,mapDispatchToProps)(RegisterSystem);