import React from 'react';
import ReactDOM from 'react-dom';
import './ambulance.css';
import Avatar from '@material-ui/core/Avatar';
import MapHandler from '../trackermap/ambulancemap.js';
import Header from '../header/header.js';
import Switch from '@material-ui/core/Switch';

class AmbulanceUser extends React.Component{
	constructor(){
		super()
		this.EnableLocation=this.EnableLocation.bind(this);
	}
	EnableLocation(){
		const renderTest=(<div className="spinner-border" id="LocationSpin"
			style={{
				position:'relative',
				left:'20px',
				top:'15px',
				backgroundColor:'white'
			}}
			></div>);
		ReactDOM.render(renderTest,document.getElementById("MyId"));
		if(navigator.geolocation){
			navigator.geolocation.getCurrentPosition((e)=>{
				document.getElementById("LocationIcons").style.color='#68FAF1';
			});
		}return ()=>alert("Not Supported Location Please Enable in Different Browsers");			

	}
	render(){
		const ambulance={boxShadow:'0px 5px',borderLeft:'none',transition:'0.3s boxShadow',color:'#1DD9D8'};

		return(
				<>
				<Header loginlink={'nav-item active'} loginstyle={ambulance}
					LocationIcon={<a className="nav-link" href="#" onClick={this.EnableLocation} id="LocationIcons"><i style={{
						color:'white',
						fontSize:'40px'
					}}

						className="fa fa-compass"></i></a>}
					homeuser={<a className="nav-link" href="/admin">Hospitals</a>}
					loginuser={<a className="nav-link" href="/Ambulance">Ambulance</a>}
					feedbackuser={<a className="nav-link" href="/accounts">Accounts</a>}
					aboutlink={<a className="nav-link" href="/about">About</a>}
					registeruser={<a className="nav-link" href="/logout"><i className="fa fa-sign-out"></i></a>}
					feedbackuser2={<Avatar id="firstletter">{this.props.usericon}T</Avatar>}

				 />
				 <MapHandler/>
				 </>

			)
	}
}
export default AmbulanceUser;