const mystate={payload1:[],payload2:[],payload3:[],payload4:[],
	payload5:[],payload6:[],payload7:[],payload8:[],payload9:[],
	payload10:[],payload11:[],payload12:[],payload13:[],payload14:[],payload15:[]};


export default function DetailUsers(state=mystate,action){
	switch(action.type){
		case 'LOGIN_USER':
			return{
				...state.payload1,
				payload1:action.payload,
			}
		case 'REGISTER_USER':
			return{
				...state,
				payload2:action.payload
			}
		case 'HOSPITAL_LIST':
			return{
				...state,
				payload3:action.payload
			}
		case 'OTP_Handler':
			return{
				...state,
				payload4:action.payload
			}
		case 'OTP_CHECK':
			return{
				...state,
				payload5:action.payload
			}
		case 'DELETE_ACCOUNT_ERROR':
			return{
				...state,
				payload6:action.payload
			}
		case 'UPDATE_ACCOUNT':
			return{
				...state,
				payload7:action.payload
			}
		case 'GET_ALL_ACCOUNT':
			return{
				...state,
				payload8:action.payload
			}
		case 'ADMIN_ACCOUNT':
			return{
				...state,
				payload9:action.payload
			}
		case 'ADMIN_DATABASE':
			return{
				...state,
				payload10:action.payload
			}
		case 'ADMIN_DETAILS':
			return{
				...state,
				payload11:action.payload
			}
		case 'COVID_MAP':
			return{
				...state,
				payload12:action.payload
			}
		case 'COVID_GRAPH':
			return{
				...state,
				payload13:action.payload
			}
		case 'USER_FEEDBACK':
			return {
				...state,
				payload14:action.payload
			}
		case 'HOSPITAL_BOOK_APPLY':
			return{
				...state,
				payload15:action.payload
			}
		default:
			return {...mystate}
	}
};
