import React from 'react';
import ReactDOM from 'react-dom';
import SearchForm from './searchform.js';
import './header.css'
import Switch from '@material-ui/core/Switch';
import {useRef,useState,useEffect} from 'react';

const Header=(props)=>{

	const [isHidden,isShow]=useState({navBarStatus:'none',bcolor:'purple',checking:false,width:'none',transition:'margin-right 0.2s',display:'none',margin:'',left:''});
		if(isHidden.checking===true){
			document.body.style.backgroundColor='black';
		}else{
			document.body.style.backgroundColor="white";
		}

	document.body.addEventListener('click',()=>{document.getElementById('mysearchformjumbo').style.display="none"})
	function Sidebarshow(e)
	{
		if(isHidden.display==="none"){
			isShow({display:'block',bcolor:'black',trans:'2s margin-right',width:'50%'})
		}else{
		isShow({display:"none",trans:'margin-left 2s'});
		}
	}
	return(
			<>
			<nav className="navbar navbar-dark bg-dark navbar-static-top navbar fixed-top" id="headerbar">
				<div className="navbar-brand">
					<h5 id="mytitle">Covid 19 Tracker</h5>
				</div>
				<div className="searchhelp">
					<center>
					<form method="get" className="form-inline" action="search.html?q=">
						<input type="text" id="myinput" autocomplete="off" onFocus={()=>isHidden.navBarStatus==="none"?isShow({navBarStatus:'inline'}):false} 
						onChange={(e)=>SearchForm(e.target.value)} className="form-control" placeholder="Search a query.."/>
						<button id="fbtn" type="submit"><i id="mybtn" className="fa fa-search"></i></button>
					</form>
					</center>
				</div>
				
					<ul className="nav nav-pills" id="myul">
						<li className="nav-item" id="MyId">{props.LocationIcon}</li>

						<Switch checked={isHidden.checking}
						onChange={(e)=>isShow({checking:e.target.checked})}/>
						<li className={props.homelink} style={props.homestyle}>{props.homeuser}</li>
						<li className={props.loginlink} style={props.loginstyle}>{props.loginuser}</li>
						<li className={props.registerlink} style={props.registerstyle}>{props.registeruser}</li>
						<li className={props.feedbacklink} style={props.feedbackstyle2}>{props.feedbackuser2}</li>

						<li className={props.feedbacklink} style={props.feedbackstyle}>{props.feedbackuser}</li>

						<li className={props.aboutlink} style={props.aboutstyle}>{props.aboutuser}</li>
					</ul>
					<div id="togglehandler">
						<a type="button"><i onClick={Sidebarshow} className='fa fa-bars'></i></a>
					</div>
					
			</nav>
			<ul id="dropitems" className='nav navbar-nav' style={{display:isHidden.display,
				backgroundColor:isHidden.bcolor,transition:isHidden.trans,width:isHidden.width

			}}>
						<div id="mybox">
							<li className="nav-item"><a className="nav-link" href="/">Home</a></li>
							<li className="nav-item"><a className="nav-link" href="/login">Login</a></li>
							<li className="nav-item"><a className="nav-link" href="/register">Signup</a></li>
							<li className="nav-item"><a className="nav-link" href="feedback">Feedback</a></li>
							<li className="nav-item"><a className="nav-link" href="/about">About</a></li>
							
					</div>
					<div id="adminHeader" style={{display:'none'}}>
							<li className="nav-item"><a className="nav-link" href="/">Location</a></li>
							<li className="nav-item"><a className="nav-link" href="/login">Hospitals</a></li>
							<li className="nav-item"><a className="nav-link" href="/register">Ambulance</a></li>
							<li className="nav-item"><a className="nav-link" href="feedback">Logout</a></li>
							<li className="nav-item"><a className="nav-link" href="/about">Accounts</a></li>
							
					</div>	
	

				</ul>
				<div className="row">
					<div className="col-sm-3"></div>
					<div className="col-lg-2">
						<ul id="mysearchformjumbo" className="nav navbar-nav"></ul>
					</div>
				</div>
			</>
		);
}
export default Header;