import React from 'react';
import  india  from './pol.png';
import  AOS from 'aos';
import {VictoryLine,VictoryTheme} from 'victory';
import $ from 'jquery';
import ReactDOM from 'react-dom';
import  L  from 'leaflet';
import {connect} from 'react-redux';
import {GraphCurrentHandler,GraphOldHandler,GraphDeathHandler,GraphActiveHandler} from './graphcovid.js';
import {GetState} from '../../actions/action.js';
import './covidmap.css';

const mapStateToProps=(state)=>{
	const m=state;
	try{
		return{
			myStatechecker:m
			}
	}catch(e){}
}


class Covid19Map extends React.Component{
	constructor(props){
		super(props);
		AOS.init();
		this.p1=0;this.p2=0;this.p3=0;this.newIds=900000;
		this.handleMount=this.handleMount.bind(this);
		this.props.TestingChecker();
		this.state={start:0,end:10,activeState:'',plus:'',recoveredState:'',deathState:'',todayDeathState:'',todayRecoveredState:'',todayCaseState:'',victoryActive:[{x:3.5,y:5.2},
		{x:4.0,y:2.44},{x:0.9,y:0.6},{x:1.3,y:0.9},{x:2.1,y:1.4},{x:3.2,y:2.1},{x:3.6,y:2.5},{x:4.0,y:2.8},{x:4.3,y:3.0},{x:4.9,y:3.4},{x:5.2,y:3.9}],
		victorydeath:[{x:2,y:1},
		{x:0,y:2},{x:1,y:3},{x:2,y:4},{x:3,y:5},{x:4,y:6},{x:6,y:7}],
		victoryrecovered:[{x:0.2,y:0.5},
		{x:2,y:2},{x:2.4,y:0},{x:3,y:-2},{x:4,y:-6},{x:6,y:-1}],	
			victoryDeath:0,
			VictoryRecovered:0	
		}

	}
	handleMount(start,end){

		const renderhandler=this.props.myStatechecker.allDetails.payload12.data.slice(0,10).map(checking=>{
					return(<li className="nav-item">
							<ul className="nav navbar-pills nav-link" style={{display:'flex',justifyContent:'space-between'}}>
								<li className="nav-item" ><span className="nav-link">{checking.State}</span></li>
								<li className="nav-item" ><span className="nav-link">{checking.TotalCase}</span></li>
								<li className="nav-item" ><span className="nav-link">{checking.Recovered}</span></li>
								<li className="nav-item" ><span className="nav-link">{checking.Deaths}</span></li>
								<li className="nav-item" ><span className="nav-link">{checking.State}</span></li>
								<li className="nav-item" ><span className="nav-link">{checking.Locality}</span></li>

							</ul><hr></hr></li>)
				});
			
			ReactDOM.render(renderhandler,document.getElementById("currentstatusofcovid"));
	}
	componentDidMount(){
		let x=parseInt(0).toLocaleString();
		const myMap=L.map(document.getElementById("heighttest")).setView([20.5937,78.9629],5);
		setTimeout(()=>{
				try{
				const indiaration=this.props.myStatechecker.allDetails.payload12.IndiaStatus;
				const totalcase=parseInt(indiaration.TotalCases.toString().replace(/,/g,""))
				const totaldeath=parseInt(indiaration.TotalDeaths.toString().replace(/,/g,""))
				const todaycase=parseInt(indiaration.TodayCases.toString().replace(/,/g,""))
				const todaydeath=parseInt(indiaration.TodayDeath.toString().replace(/,/g,""))
				const todayrecovered=parseInt(indiaration.TodayRecovered.toString().replace(/,/g,""))
				const totalrecovered=parseInt(indiaration.TotalRecoverd.toString().replace(/,/g,""))
				this.setState(previous=>({activeState:totalcase,recoveredState:totalrecovered,deathState:totaldeath,
					todayDeathState:todaydeath,todayRecoveredState:todayrecovered,todayCaseState:todaycase,plus:'+'}));

				$('.counter').each(function() {
				  var $this = $(this),
				      countTo = $this.attr('data-count');
				  
				  $({ countNum: $this.text()}).animate({
				    countNum: countTo
				  },

				  {

				    duration: 3000,
				    easing:'linear',
				    step: function() {
				      $this.text(Math.floor(this.countNum).toLocaleString('en'));
				    },
				    complete: function() {
				      $this.text(this.countNum.toLocaleString('en'));
				    }

				  });
			})  
			}
				catch(e){}
		try{
			this.props.myStatechecker.allDetails.payload12.data.map(keyhandler=>{
			try{

			const cord=keyhandler.Cordinates.split(",");
			const lang = cord[0].replace("xb0","").replace("S","").replace("N","")
			const long  = cord[1].replace("xb0","").replace("W","").replace("E","")
			const tooltiphandler="<center>" + keyhandler.Locality + "</center>"+"<br>"+"<i class='fa fa-circle' style='color:red;'></i> TotalCases: "
						+keyhandler.TotalCase+"<br>"+"<i class='fa fa-circle' style='color:black;'></i> Deaths: "+keyhandler.Deaths
						+"<br>"+"<i class='fa fa-circle' style='color:green;'></i> TotalRecovered: "+keyhandler.Recovered
						+"<br>"+"<i class='fa fa-circle' style='color:blue;'></i> TodayCases: "+keyhandler.TodayCases
						+"<br>"+"<i class='fa fa-circle' style='color:green;'></i> TodayRecovered: "+keyhandler.TodayRecovered
						+"<br>"+"<i class='fa fa-circle' style='color:black;'></i> TodayDeath: "+keyhandler.TodayDeath
			L.circleMarker([lang,long],{radius:5,color:'blue',weight:0}).bindTooltip(tooltiphandler).addTo(myMap)

			}catch(e){
				console.log(e)
			}
			const h=document.getElementById("currentstatusofcovid");
			$(window).on("scroll",()=>{
					ReactDOM.render(<div className="spinner-border" style={{height:'50px',width:'50px'}}></div>,document.getElementById("spincenter"));
					setTimeout(()=>{
						this.start+=10;this.end+=10;
						this.handleMount(this.state.start,this.state.end)
					},1000);
				
			});
		})}catch(e){}
	},1000);
			L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}', {
		    	maxZoom: 15,
		    	fullscreenControl:true,
		   	 	id: 'mapbox/streets-v11',
		    	attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
		    	tileSize: 512,
		    	zoomOffset: -1,
			    accessToken: 'pk.eyJ1Ijoic3VwZXJndXNocGEiLCJhIjoiY2twZmJsZWZxMGdkcTJvcmkzdHAxbGhpYiJ9.uDRrLbheGeP2BnKcO69coQ'
			}).addTo(myMap);
	}
	render()
	{
		return(
				<div className="container">
					<div className="row">
						<div className="map-show">
							<div className="d-flex justify-content-center w-100">
							<div className="card shadow p-4" data-aos="fade-down"  data-aos-once="true" data-aos-duration="1500" id="updatecard">
								<div className="card-body">
									<center>
										<h4 id="headingtotal">Total Cases</h4>
									</center>
									<ul className="nav nav-pills" id='caseul'>
										<li className="nav-item">
											<div className="jumbotron" id="fl" >
												<b><center><span>Active</span></center></b>
												<br/><br/>
												<center><span id="activecase" data-count={this.state.activeState} className="counter">{0}</span>
												<br />
												<i className="fa fa-arrow-up">{this.state.plus}<span data-count={this.state.todayCaseState} className="counter">0</span></i>
												<br />
													<VictoryLine theme={VictoryTheme.material} style={{data:{stroke:'red',strokeWidth:1.3,weight:3}}} height={200} width={50} data={this.state.victoryActive} animate={{duration:6000,onLoad:{duration:5000}}} interpolation="natural" />
												</center>
																						
											</div>									
										</li>
										<li className="nav-item">
											<div className="jumbotron">
												<b><center><span>Recovered</span></center></b>
												<br/><br/>
												<center><span id="recovered" data-count={this.state.recoveredState} className="counter">0</span><br />
													<i className="fa fa-arrow-up">{this.state.plus}<span data-count={this.state.todayRecoveredState} className="counter">0</span></i>
													<VictoryLine data={this.state.victoryrecovered}  style={{data:{stroke:'blue',strokeWidth:1.3}}} height={200} width={50} interpolation="natural" animate={{duration:6000,onLoad:{duration:5000}}} />
												</center>
											</div>
										</li>
										<li className="nav-item">
											<div className="jumbotron" >
												<b><center><span>Deceased</span></center></b>
												<br/><br/>
												<center><span id="death" data-count={this.state.deathState} className="counter">0</span>
													<br />
													<i className="fa fa-arrow-up">{this.state.plus}<span data-count={this.state.todayDeathState} className="counter">0</span></i>
													<VictoryLine  height={200} data={this.state.victorydeath} width={50} style={{data:{stroke:'black',strokeWidth:1.3,strokeHeight:1}}} interpolation="natural" animate={{duration:6000,onLoad:{duration:5000}}} />
												</center>
											</div>
										</li>
									</ul>
								</div>
							</div>										
							</div>
							<div className="col-sm-6">
								<div id="heighttest" data-aos-once="true" data-aos="fade-down" data-aos-duration="1500" className="shadow p-4">

								</div>
									
							</div>
						</div>

						<div className="col-lg-12">
							<br/>
							<div id="result" id="resultnav">
								<ul className="nav navbar-pills" id="headtitle">
									<div className="jumbotron" id="a">
										<li>States</li>
									</div>
									<div className="jumbotron" id="b">
										<li>Active</li>
									</div>
									<div className="jumbotron" id="c">
										<li>Recovered</li>
									</div>
									<div className="jumbotron" id="d">
										<li>Deceased</li>
									</div>
									<div className="jumbotron" id="e">
										<li>Tested</li>
									</div>
									<div className="jumbotron" id="f">
										<li>Locality</li>
									</div>
								</ul>
								
							</div>
									<ul className="nav navbar-nav jumbotron" id="currentstatusofcovid">

									</ul>	
									<center id="spincenter"></center>
							</div>
							<div className="row">
								<div className="col-lg-6">
									<center><GraphCurrentHandler 
									ystart={0} yend={12}

									xstart={5000} xend={this.state.activeState}/></center>
								</div>

								<div className="col-lg-6">
									<GraphOldHandler />
								</div>
							</div>
							<div className="row">

								<div className="col-lg-6">
									<center><GraphDeathHandler /></center>
								</div>
								<div className="col-lg-6">
									<GraphActiveHandler />
								</div>
							</div>
					</div>
			</div>

			);
	}
}
const mapDispatchToprops=dispatch=>{
	return{
		TestingChecker:()=>{
			dispatch(GetState())
		}
	}
}

export default connect(mapStateToProps,mapDispatchToprops)(Covid19Map);