import React,{useState} from 'react';
import {useSelector,useDispatcher} from 'react-redux';
import {DatabaseHandler} from '../../actions/action.js';
import {connect} from 'react-redux';
import $ from 'jquery';
import {GetDatabase} from '../../actions/action.js';
import './database.css';

const mapStateToProps=(state)=>{
	const mydata=state.allDetails.payload10.data;
	try{
	const finalData=mydata.map(s=>{
		return(
			<li className="nav-item">
				<ul className="nav nav-pills form-control">
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Hospitalname}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Reservebeds}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Vacantbeds}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Ventilatorreserved}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Ventilatorvaccant}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.State}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.City}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Telephone}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Totalbeds}/></li>
					<li className="nav-item"><input type="text" className="nav-link form-control disabled" value={s.Totalventilators}/></li>
				</ul>
			</li>
		)
	})
	return{
		myFinalData:finalData
	}}catch(E){}
}

class DataBase extends React.Component{
	constructor(props){
		super(props);
	}
	componentDidMount(){

		const yAll=document.querySelectorAll("#databaselist >li >ul");
		const newArray=[...yAll]
		newArray.map(keyhandler=>{
				[...keyhandler.children].map(ps=>{
					$(ps).on("dblclick",(e)=>{
						e.target.childNodes[0].setAttribute("class","nav-link");
					})
					
				})
			})
	}
	render(){
		return(
				<div id="dbul" style={this.props.checkEnabled}>
					<ul className="nav nav-pills" id="admindbul">
						<li className="nav-item"><span className="nav-link">Hospital-N</span></li>
						<li className="nav-item"><span className="nav-link">Reservebeds</span></li>
						<li className="nav-item"><span className="nav-link">Vacantbeds</span></li>
						<li className="nav-item"><span className="nav-link">Ventilator-R</span></li>
						<li className="nav-item"><span className="nav-link">VentilatorVaccant</span></li>
						<li className="nav-item"><span className="nav-link">StateWise</span></li>
						<li className="nav-item"><span className="nav-link">City</span></li>
						<li className="nav-item"><span className="nav-link">Telephone</span></li>
						<li className="nav-item"><span className="nav-link">Totalbeds</span></li>
						<li className="nav-item"><span className="nav-link">Totalventilators</span></li>
					</ul>
					<ul className="nav nav-pills" id="databaselist">
					{
						this.props.myFinalData
					}
					
					</ul>
				</div>
		);
	}
}
const mapDispatchToProps=dispatch=>{
	dispatch(GetDatabase(localStorage))
}
export default connect(mapStateToProps,mapDispatchToProps)(DataBase);