import React from 'react';
import ReactDOM from 'react-dom';
import './accounts.css';
import Header from '../header/header.js';
import AOS from 'aos';
import {connect} from 'react-redux';
import Switch from '@material-ui/core/Switch';
import {UpdateAccount,DeleteAccount,GetAccount} from '../../actions/action.js';
import Footer from '../footer/footer.js';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';

const mapStateToProps=({state,ownProps})=>{
	if(ownProps != undefined){
	const Test=ownProps.state
	try{
	if(state.allDetails.payload6.status==="Failed"){

		setTimeout(()=>{const x=(<div className="alert alert-danger" data-aos='fade-down' data-aos-duration='500' style={{width:'100%',backgroundColor:'red',color:'white'}}><center>{state.allDetails.payload6.message}</center></div>)
			ReactDOM.render(x,document.getElementById("ErrorDeleteAccount"));},500);

		setTimeout(()=>{ReactDOM.render("",document.getElementById("ErrorDeleteAccount"))
			document.getElementById("mydeletebtn").setAttribute("class","btn btn-default")
					ReactDOM.render(ownProps,document.getElementById("mydeletebtn"))

			},2500);
		}

	}catch(e){setTimeout(()=>{window.location='http://htmltemplate.pythonanywhere.com/register'},2000)}

	return{
		mest:Test
	}}
}
const Enabler=(e)=>{
	const checker=document.querySelectorAll("#myjumbo > input");
	checker.forEach(keychecker=>{
		keychecker.removeAttribute("disabled")
	});
}
class AccountsUser extends React.Component{

	constructor(props){
		super(props);
		this.state={isDeleteEnabled:'btn btn-default disabled',DeleteEmail:'',DeletePassword:'',SwitchState:false,headingstatus:'Temporary Delete',color:'red',
			UpdateState:{firstname:'',lastname:'',password:'',email:'',phone:'',
			'authorization':localStorage.getItem("authorization")
		}};
		this.Updatehandler=this.Updatehandler.bind(this);
		this.Deletefunction=this.Deletefunction.bind(this);
		this.UpdateSystem=this.UpdateSystem.bind(this);
		this.Deletehandler=this.Deletehandler.bind(this);
		AOS.init();

	}

	Deletehandler(e){
		this.setState({[e.target.name]:e.target.value})
		if(this.state.DeleteEmail.length >= 10 && this.state.DeletePassword.length >=10){
			this.setState({isDeleteEnabled:'btn btn-default'})
		}else{	
			this.setState({isDeleteEnabled:'btn btn-default disabled'})
		}
	}
	Deletefunction(e){
		this.setState({isDeleteEnabled:'btn btn-default disabled'})
		const g=(<div className="spinner-border"></div>);

		ReactDOM.render(g,e.target);
		this.props.CallerDelete({username:this.state.DeleteEmail,
			password:this.state.DeletePassword,type:this.state.SwitchState,authorization:this.state.UpdateState.authorization})
		
	}
	componentDidMount(){

		this.props.GetDetail({authorization:this.state.UpdateState.authorization});

	}
	UpdateSystem(e){
		const UpdateState=this.state.UpdateState;
		UpdateState[e.target.name]=e.target.value;
		this.setState(UpdateState);
		console.log(this.state.UpdateState);
	}
	Updatehandler(e){
		document.getElementById("deletetest").setAttribute("class","btn btn-default disabled");
		e.target.setAttribute("class","btn btn-default disabled");

		const g=(<div className="spinner-border"></div>);
		ReactDOM.render(g,e.target);
		this.props.UpdateCaller(this.state.UpdateState);
	}
	render(){
		const feedbackstyle={boxShadow:'0px 5px',borderLeft:'none',transition:'0.3s boxShadow',color:'#1DD9D8'};

		return(
				<>
					<Header feedbacklink={'nav-item active'} feedbackstyle={feedbackstyle}
						homeuser={<a className="nav-link" href="/admin">Hospitals</a>}
						loginuser={<a className="nav-link" href="/Ambulance">Ambulance</a>}
						feedbackuser={<a className="nav-link" href="/accounts">Accounts</a>}
						aboutlink={<a className="nav-link" href="/about">About</a>}
						registeruser={<a className="nav-link" href="/logout"><i className="fa fa-sign-out"></i></a>}
					 />
					 <br/>
					 <div className="row">
					 	<div className="col-lg-3"></div>
					 	<div className="col-lg-6">
						 	<div className="form-group jumbotron" id="myjumbo">
						 		<span id="SuccessResult"></span>
						 		<div id="flexer">
							 		<h4 id="accounth4">Account Settings</h4>
							 			<a type="button" className="btn btn-default">
							 			<i id="myeditpencil" onClick={Enabler}className="fa fa-pencil"></i>
							 		</a>
							 	</div>
							 	<br/>
								 	<input type="text" onChange={this.UpdateSystem} disabled name="firstname" placeholder="Firstname" className="form-control"/>
							 	<br/>
								 	<input type="text" onChange={this.UpdateSystem} disabled name="lastname" placeholder="Lastname" className="form-control"/>
								 <br/>
								 	<input type="email" onChange={this.UpdateSystem}  disabled name="email" placeholder="example@mail.com" className="form-control"/>
								 <br/>
								 	<input type="text" onChange={this.UpdateSystem} disabled name="phone" placeholder="Phone No." className="form-control"/>
								 <br/>
								 	<input type="password" onChange={this.UpdateSystem} disabled name="password" placeholder="Password" className="form-control"/>
								 <br/>
								 	<a type="button"  id="deletetest" data-target="#Targetmodel" data-toggle="modal" className="btn btn-link">Delete Account</a>
								 	<a type="button" onClick={this.Updatehandler} id="updatebtntest" className="btn btn-default">Update </a>
								 <br/>
							</div>
							 	<div className="modal fade" id="Targetmodel" role="dialog" aria-hidden="true">
							 		<div className="modal-dialog" role="document">
							 			<div className="modal-content">
							 				<span id="ErrorDeleteAccount"></span>

							 				<div className="modal-header">
							 					
							 					<div className="modal-title" id="headtitles">
							 						<h5  id='modalheading'>Confirm To Delete Account</h5>
							 					</div>
							 					<button type="button" className="close" data-dismiss="modal" aria-label="close"></button>
							 					<Switch checked={this.state.SwitchState}
							 						onChange={
							 							()=>
							 							this.state.SwitchState==true?this.setState({SwitchState:false,color:'red',headingstatus:'Permanent Delete'}):this.setState({...this.state,SwitchState:true,headingstatus:'Temporary Delete',color:'orange'})}
							 					/>
							 				</div>
							 				<div className="modal-body">{
							 					this.props.mest
							 				}
											 	<div className="form-group" id="deleteform">
															 		<input className="form-control" name="DeleteEmail" onChange={this.Deletehandler} required type="text" placeholder="CurrentEmail"/>
											 		<br/>
											 		<input type="password" name="DeletePassword" onChange={this.Deletehandler} required className="form-control" placeholder="Password"/>
											 		<br/>
											 		<a type="button"  className={this.state.isDeleteEnabled} onClick={this.Deletefunction} style={{backgroundColor:this.state.color}} id="mydeletebtn">{this.state.headingstatus}</a>
											 	</div>
											 </div>
										</div>
							 		</div>
							 	</div>
						 	</div>
					</div>
				<Footer />
				 </>

			); 
	}	
}
const mapDispatchToProps=dispatch=>({
			GetDetail:(data)=>dispatch(GetAccount(data)),

			CallerDelete:(data)=>dispatch(DeleteAccount(data)),
			UpdateCaller:(data)=>dispatch(UpdateAccount(data)),
});
export default connect(mapStateToProps,mapDispatchToProps)(AccountsUser);